@import "../../vars.scss";

.ezdropdown {
   align-items: center;
   border: 1px solid $dark50;
   border-radius: 4px;
   margin-top: 5px;
   margin-bottom: 10px;
   cursor: pointer;
   display: flex;
   height: $btn-height;
   justify-content: space-between;
   padding: 0 15px;
   position: relative;
   &-open {
         border-bottom-left-radius: 0px;
         border-bottom-right-radius: 0px;
      }

   &__selection {
      line-height: 18px;
      flex: 1;
      position: relative;
      &:after {
         @extend .down-arrow;
         right: 0;
         top: 20%;
      }
   }
   &__menu {
      background-color: #ffffff;
      border: 1px solid $dark50;
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      box-sizing: border-box;
      box-shadow: 0px 10px 20px $dark5;
      cursor: default;
      display: none;
      left: 0;
      margin: -2px -1px -1px;
      position: absolute;
      right: 0;
      top: 44px;
      transition: all 0.2s ease-in-out;
      z-index: 99999;
      &-open {
         display: block;
      }
   }
   &__row {
      @extend .font-label;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: $btn-height;
      padding: 0px 7px 0px 15px;
   }
   &__rowTitle {
   }
   &__rowAction {
      display: flex;
      width: 92px;
      justify-content: space-between;
      align-items: center;
   }
   &__buttons {
      @extend .ezdropdown__row;
      @extend .font-label;
   }
   &__button {
      cursor: pointer;
      border-radius: 4px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      height: 32px;
      color: $primary-color;
      font-size: 12px;
      line-height: 15px;
      &:hover {
         background: #f0f0f0;
      }
   }
}
.counterBtn {
   background: #ffffff;
   align-items: center;
   border: 1px solid $dark25;
   border-radius: 50%;
   display: flex;
   height: 30px;
   width: 30px;
   transition: all 0.2s ease-in-out;
   padding-left: 10.5px;
   color: $dark50;

   &:hover {
      background: #f0f0f0;
   }
}
.counter {
}
.open {
}
.disable {
   opacity: 0.35;
   cursor: default;
   &:hover {
      background: none;
   }
}
.hide {
   opacity: 0;
   visibility: hidden;
}
