/* -------------------------------------------------
     Datepicker cells
    ------------------------------------------------- */
.datepicker--cells {
   display: -webkit-flex;
   display: -ms-flexbox;
   display: flex;
   -webkit-flex-wrap: wrap;
   -ms-flex-wrap: wrap;
   flex-wrap: wrap;
}

.datepicker--cell {
   border-radius: 50%;
   box-sizing: border-box;
   cursor: pointer;
   display: -webkit-flex;
   display: -ms-flexbox;
   display: flex;
   position: relative;
   -webkit-align-items: center;
   -ms-flex-align: center;
   align-items: center;
   -webkit-justify-content: center;
   -ms-flex-pack: center;
   justify-content: center;
   height: 40px;
   width: 100%;
   z-index: 1;
   color: $dark50;
}
.datepicker--cell.-focus- {
   background: #f0f0f0;
}
.datepicker--cell.-current- {
   color: #ffffff;
   background: $secondary-gradient;
   font-weight: 700;
}
.datepicker--cell.-current-.-focus- {
   background: $secondary-color;
}
.datepicker--cell.-current-.-in-range- {
   color: #ffffff;
   background: $secondary-color;
   border-radius: 50%;
   &:hover {
      background: $secondary-color;
   }
}
.datepicker--cell.-in-range- {
   // background: $primary-gradient;
   color: $dark50;
   border-radius: 0;
   &::after {
      position: absolute;
      // left: 16px;
      content: "";
      height: 100%;
      width: 100%;
      background: $primary-gradient;
      opacity: 0.25;
      z-index: -999;
 }

}
.datepicker--cell.-in-range-.-focus- {
   background: rgba(0, 0, 0, 0.05);
   border-radius: 50%;
}
.datepicker--cell.-disabled- {
   cursor: default;
   color: $dark25;
}
.datepicker--cell.-disabled-.-focus- {
   color: #aeaeae;
}
.datepicker--cell.-disabled-.-in-range- {
   color: #a1a1a1;
}
.datepicker--cell.-disabled-.-current-.-focus- {
   color: #aeaeae;
}
.datepicker--cell.-range-from- {
   border-radius: 50%;
   font-weight: 700;
   &:hover {
      background: $primary-gradient;
      color: #ffffff;
   }

   &::after {
      position: absolute;
      left: 20px;
      content: "";
      height: 100%;
      width: 50%;
      background: $primary-gradient;
      opacity: 0.25;
      z-index: -999;
 }
}
.datepicker--cell.-range-to- {
   border-radius: 50%;
   font-weight: 700;
   &:hover {
      background: $primary-gradient;
      color: #ffffff;
   }
   &::after {
      position: absolute;
      right: 20px;
      content: "";
      height: 100%;
      width: 50%;
      background: $primary-gradient;
      opacity: 0.25;
      z-index: -999;
 }
}
.datepicker--cell.-range-from-.-range-to- {
   border-radius: 4px;
}
.datepicker--cell.-selected- {
   color: #fff;
   background: $primary-gradient;
}
.datepicker--cell.-selected-.-current- {
   color: #fff;
   background: $primary-gradient;
}
.datepicker--cell.-selected-.-focus- {
   background: $primary-gradient;
}
.datepicker--cell:empty {
   cursor: default;
}

