/* -------------------------------------------------
     Navigation
    ------------------------------------------------- */
.datepicker--nav {
   display: -webkit-flex;
   display: -ms-flexbox;
   display: flex;
   -webkit-justify-content: space-between;
   -ms-flex-pack: justify;
   justify-content: space-between;
   min-height: 32px;
   width: 280px;
   margin: 20px auto;
}
.-only-timepicker- .datepicker--nav {
   display: none;
}

.datepicker--nav-title,
.datepicker--nav-action {
   display: -webkit-flex;
   display: -ms-flexbox;
   display: flex;
   cursor: pointer;
   -webkit-align-items: center;
   -ms-flex-align: center;
   align-items: center;
   -webkit-justify-content: center;
   -ms-flex-pack: center;
   justify-content: center;
   @extend .font-subtitle;
}

.datepicker--nav-action {
   width: 32px;
   border-radius: 4px;
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
}
.rightArrow,
.leftArrow {
   position: relative;
   bottom: 4px;
   left: 10px;
   width: 15px;
   height: 10px;
   border-bottom: 2px solid;
   border-color: $primary-color;
   
   &:after{
     position: absolute;
     content: "";
     display: block;
     width: 10px;
     height: 10px;
     border-top: 2px solid;
     border-right: 2px solid;
     border-color: $primary-color;
     color: $primary-color;
   }
}
.rightArrow {
   left: 4px;
   &:after{
      top: 4px;
      left: 5px;
     transform: rotate(45deg);
   }
}
.leftArrow {
   left: -4px;
   &:after{
      top: 4.4px;
      left: 0px;
     transform: rotate(225deg);
   }
}

.datepicker--nav-action:hover {
   background: #f0f0f0;
}
.datepicker--nav-action.-disabled- {
   visibility: hidden;
}
.datepicker--nav-action svg {
   width: 32px;
   height: 32px;
}
.datepicker--nav-action path {
   fill: none;
   stroke: #9c9c9c;
   stroke-width: 2px;
}

.datepicker--nav-title {
   border-radius: 4px;
   padding: 0 8px;
}
.datepicker--nav-title i {
   font-style: normal;
   margin-left: 5px;
}
.datepicker--nav-title:hover {
   background: #f0f0f0;
}
.datepicker--nav-title.-disabled- {
   cursor: default;
   background: none;
}

.datepicker--buttons {
   display: -webkit-flex;
